import React from "react";
import {getInstanceAvaxSki,getWeb3}  from "../Web3Util";
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import {Link} from 'react-router-dom';


import skiteampromo from '../images/skiteampromo.png'; 
import skiteamfulllogo from '../images/skiteamfulllogo.png'; 
import background1 from '../images/background1.png'; 
import background2 from '../images/background2.png'; 
import background3 from '../images/background3.png'; 

import skiteamlogo from '../images/skiteamlogo.png'; 

import wsb from '../images/wsb.jpeg'; 

import rotating1 from '../images/rotating/11.png'
import rotating2 from '../images/rotating/12.png'
import rotating3 from '../images/rotating/13.png'
import rotating4 from '../images/rotating/14.png'
import rotating5 from '../images/rotating/15.png'
import rotating6 from '../images/rotating/16.png'
import rotating7 from '../images/rotating/17.png'
import rotating8 from '../images/rotating/18.png'
import rotating9 from '../images/rotating/19.png'
import rotating10 from '../images/rotating/20.png'

import skigif from '../images/skigif.gif'

let rotatingArray= [ rotating1,rotating2,rotating3,rotating4,rotating5,
  rotating6,rotating7,rotating8,rotating9,rotating10]

const SKI_SUPPLY = 10000
const SKI_PRICE = 2.5

// Date and time (GMT): Tuesday, October 26, 2021 2:30:35 PM
const TIMESTAMP  = 558635 
var seconds = TIMESTAMP;

class HomePage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      hasHomepageData: false,
      isLoadingHomepageData:true,
      nftIndexAvaxSki:-100,
      avaxSkiSelect:1,
      isMintingAvaxSki:false,
      isClaimingRewards:false,
      myRewards:0,
      totalSupply:0,
      price:0,
      currentImage: 0,
      timeLeft:""
    };
    this.mint = this.mint.bind(this);
    this.tick = this.tick.bind(this);
    this.decreaseSelect = this.decreaseSelect.bind(this)
    this.increaseSelect = this.increaseSelect.bind(this)
    this.claimRewards = this.claimRewards.bind(this)
    this.loadData = this.loadData.bind(this)
  }
  tick() {
    if(this.state.currentImage<9){
      this.setState(state => ({
        currentImage: state.currentImage + 1
      }));
    }else if(this.state.currentImage==9){
      this.setState(state => ({
        currentImage: 0
      }));
    }

    var days        = Math.floor(seconds/24/60/60);
    var hoursLeft   = Math.floor((seconds) - (days*86400));
    var hours       = Math.floor(hoursLeft/3600);
    var minutesLeft = Math.floor((hoursLeft) - (hours*3600));
    var minutes     = Math.floor(minutesLeft/60);
    var remainingSeconds = seconds % 60;
    function pad(n) {
      return (n < 10 ? "0" + n : n);
    }

    this.setState({
      timeLeft:pad(days) + "D:" + pad(hours) + "H:" + pad(minutes) + "M:" + pad(remainingSeconds)+"S"
    })
    if (seconds == 0) {
      this.setState({
        timeLeft:"Minting live!"
      })
    } else {
      seconds--;
    }
  }
  componentDidMount = async () => {
    try{
      const web3 = await getWeb3();
      window.web3 = web3;
      const contractInstanceAvaxSki = await getInstanceAvaxSki(window.web3);

      this.setState({ user: window.user });
      this.setState({ contractInstanceAvaxSki});
      await this.loadData();
    } catch (e) {
      console.log('Error', e)
    }finally{
      this.setState({
        isLoadingHomepageData:false
      })
    }
    this.interval = setInterval(() => this.tick(), 250);

  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  async loadData() {
      try {
        const totalSupply = await this.state.contractInstanceAvaxSki.methods.totalSupply().call();
        this.setState({ totalSupply });
        
        this.setState({hasHomepageData:true})
    } catch (e) {
      console.log('Error', e)
    }
  }

  increaseSelect(){
    if(this.state.avaxSkiSelect<20){
      this.setState({
        avaxSkiSelect:this.state.avaxSkiSelect+1
      })
    }
  }
  
  decreaseSelect(){
    if(this.state.avaxSkiSelect>1){
      this.setState({
        avaxSkiSelect:this.state.avaxSkiSelect-1
      })
    }
  }

  async mint(){
    try {
        this.setState({isMinting:true})
        let localValue = window.web3.utils.toWei((this.state.avaxSkiSelect*SKI_PRICE)+"", 'ether')

        await this.state.contractInstanceAvaxSki.methods.mint(this.state.avaxSkiSelect).send(
          {value:localValue
          }
        )
        await this.loadData()
      } catch (e) {
      console.log('Error', e)
    }finally{
      this.setState({isMinting:false})
    }
  }

  async claimRewards(){
    try {
        this.setState({isClaimingRewards:true})

        await this.state.contractInstanceAvaxSki.methods.claimRewards().send()
        await this.loadData()
      } catch (e) {
      console.log('Error', e)
    }finally{
      this.setState({isClaimingRewards:false})
    }
  }
  
  render() {
    console.log("@@@ window.ethereum",window.ethereum)
    return(
       <>
          <header className="header-ski">
           <div className="logo-holder">
             <img className="logo" src={skiteamfulllogo} />
           </div>
           <div className="rest-holder">
            <div className="mobile-hide" >
              <a href="https://twitter.com/AvaxSkiTeam" target="_blank">
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="black" d="M24 4.557c-.883.392-1.832.656-2.828.775 1.017-.609 1.798-1.574 2.165-2.724-.951.564-2.005.974-3.127 1.195-.897-.957-2.178-1.555-3.594-1.555-3.179 0-5.515 2.966-4.797 6.045-4.091-.205-7.719-2.165-10.148-5.144-1.29 2.213-.669 5.108 1.523 6.574-.806-.026-1.566-.247-2.229-.616-.054 2.281 1.581 4.415 3.949 4.89-.693.188-1.452.232-2.224.084.626 1.956 2.444 3.379 4.6 3.419-2.07 1.623-4.678 2.348-7.29 2.04 2.179 1.397 4.768 2.212 7.548 2.212 9.142 0 14.307-7.721 13.995-14.646.962-.695 1.797-1.562 2.457-2.549z"/></svg>
              </a>
            </div>
            <div className="mobile-hide" style={{marginRight:"15px"}}>
              <a href="https://discord.com/invite/XykWhX5vn7" target="_blank">
                <svg xmlns="http://www.w3.org/2000/svg" width="54" height="22" viewBox="0 0 71 55" fill="none">
                <g clip-path="url(#clip0)">
                <path d="M60.1045 4.8978C55.5792 2.8214 50.7265 1.2916 45.6527 0.41542C45.5603 0.39851 45.468 0.440769 45.4204 0.525289C44.7963 1.6353 44.105 3.0834 43.6209 4.2216C38.1637 3.4046 32.7345 3.4046 27.3892 4.2216C26.905 3.0581 26.1886 1.6353 25.5617 0.525289C25.5141 0.443589 25.4218 0.40133 25.3294 0.41542C20.2584 1.2888 15.4057 2.8186 10.8776 4.8978C10.8384 4.9147 10.8048 4.9429 10.7825 4.9795C1.57795 18.7309 -0.943561 32.1443 0.293408 45.3914C0.299005 45.4562 0.335386 45.5182 0.385761 45.5576C6.45866 50.0174 12.3413 52.7249 18.1147 54.5195C18.2071 54.5477 18.305 54.5139 18.3638 54.4378C19.7295 52.5728 20.9469 50.6063 21.9907 48.5383C22.0523 48.4172 21.9935 48.2735 21.8676 48.2256C19.9366 47.4931 18.0979 46.6 16.3292 45.5858C16.1893 45.5041 16.1781 45.304 16.3068 45.2082C16.679 44.9293 17.0513 44.6391 17.4067 44.3461C17.471 44.2926 17.5606 44.2813 17.6362 44.3151C29.2558 49.6202 41.8354 49.6202 53.3179 44.3151C53.3935 44.2785 53.4831 44.2898 53.5502 44.3433C53.9057 44.6363 54.2779 44.9293 54.6529 45.2082C54.7816 45.304 54.7732 45.5041 54.6333 45.5858C52.8646 46.6197 51.0259 47.4931 49.0921 48.2228C48.9662 48.2707 48.9102 48.4172 48.9718 48.5383C50.038 50.6034 51.2554 52.5699 52.5959 54.435C52.6519 54.5139 52.7526 54.5477 52.845 54.5195C58.6464 52.7249 64.529 50.0174 70.6019 45.5576C70.6551 45.5182 70.6887 45.459 70.6943 45.3942C72.1747 30.0791 68.2147 16.7757 60.1968 4.9823C60.1772 4.9429 60.1437 4.9147 60.1045 4.8978ZM23.7259 37.3253C20.2276 37.3253 17.3451 34.1136 17.3451 30.1693C17.3451 26.225 20.1717 23.0133 23.7259 23.0133C27.308 23.0133 30.1626 26.2532 30.1066 30.1693C30.1066 34.1136 27.28 37.3253 23.7259 37.3253ZM47.3178 37.3253C43.8196 37.3253 40.9371 34.1136 40.9371 30.1693C40.9371 26.225 43.7636 23.0133 47.3178 23.0133C50.9 23.0133 53.7545 26.2532 53.6986 30.1693C53.6986 34.1136 50.9 37.3253 47.3178 37.3253Z" fill="black"/>
                </g>
                <defs>
                <clipPath id="clip0">
                <rect width="71" height="55" fill="white"/>
                </clipPath>
                </defs>
                </svg>
              </a>
            </div>
            <div className="invisible">
              Hall of fame
            </div>
            <div>
              <Link to="/" className="menu-link disabled">
              My Skiers
              </Link>
            </div>
            <div>
              <Link to="/" className="menu-link disabled">
              Market
              </Link>
            </div>
            <div>
              <Link to="/" className="menu-link">
              Home
              </Link>
            </div>
           </div>
          <div className="mobile-show menu-mobile">
            <div style={{float:"left"}} >
                <a href="https://twitter.com/AvaxSkiTeam" target="_blank">
                  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="black" d="M24 4.557c-.883.392-1.832.656-2.828.775 1.017-.609 1.798-1.574 2.165-2.724-.951.564-2.005.974-3.127 1.195-.897-.957-2.178-1.555-3.594-1.555-3.179 0-5.515 2.966-4.797 6.045-4.091-.205-7.719-2.165-10.148-5.144-1.29 2.213-.669 5.108 1.523 6.574-.806-.026-1.566-.247-2.229-.616-.054 2.281 1.581 4.415 3.949 4.89-.693.188-1.452.232-2.224.084.626 1.956 2.444 3.379 4.6 3.419-2.07 1.623-4.678 2.348-7.29 2.04 2.179 1.397 4.768 2.212 7.548 2.212 9.142 0 14.307-7.721 13.995-14.646.962-.695 1.797-1.562 2.457-2.549z"/></svg>
                </a>
              </div>
              <div  style={{marginRight:"15px",float:"left"}}>
                <a href="https://discord.com/invite/XykWhX5vn7" target="_blank">
                  <svg xmlns="http://www.w3.org/2000/svg" width="54" height="22" viewBox="0 0 71 55" fill="none">
                  <g clip-path="url(#clip0)">
                  <path d="M60.1045 4.8978C55.5792 2.8214 50.7265 1.2916 45.6527 0.41542C45.5603 0.39851 45.468 0.440769 45.4204 0.525289C44.7963 1.6353 44.105 3.0834 43.6209 4.2216C38.1637 3.4046 32.7345 3.4046 27.3892 4.2216C26.905 3.0581 26.1886 1.6353 25.5617 0.525289C25.5141 0.443589 25.4218 0.40133 25.3294 0.41542C20.2584 1.2888 15.4057 2.8186 10.8776 4.8978C10.8384 4.9147 10.8048 4.9429 10.7825 4.9795C1.57795 18.7309 -0.943561 32.1443 0.293408 45.3914C0.299005 45.4562 0.335386 45.5182 0.385761 45.5576C6.45866 50.0174 12.3413 52.7249 18.1147 54.5195C18.2071 54.5477 18.305 54.5139 18.3638 54.4378C19.7295 52.5728 20.9469 50.6063 21.9907 48.5383C22.0523 48.4172 21.9935 48.2735 21.8676 48.2256C19.9366 47.4931 18.0979 46.6 16.3292 45.5858C16.1893 45.5041 16.1781 45.304 16.3068 45.2082C16.679 44.9293 17.0513 44.6391 17.4067 44.3461C17.471 44.2926 17.5606 44.2813 17.6362 44.3151C29.2558 49.6202 41.8354 49.6202 53.3179 44.3151C53.3935 44.2785 53.4831 44.2898 53.5502 44.3433C53.9057 44.6363 54.2779 44.9293 54.6529 45.2082C54.7816 45.304 54.7732 45.5041 54.6333 45.5858C52.8646 46.6197 51.0259 47.4931 49.0921 48.2228C48.9662 48.2707 48.9102 48.4172 48.9718 48.5383C50.038 50.6034 51.2554 52.5699 52.5959 54.435C52.6519 54.5139 52.7526 54.5477 52.845 54.5195C58.6464 52.7249 64.529 50.0174 70.6019 45.5576C70.6551 45.5182 70.6887 45.459 70.6943 45.3942C72.1747 30.0791 68.2147 16.7757 60.1968 4.9823C60.1772 4.9429 60.1437 4.9147 60.1045 4.8978ZM23.7259 37.3253C20.2276 37.3253 17.3451 34.1136 17.3451 30.1693C17.3451 26.225 20.1717 23.0133 23.7259 23.0133C27.308 23.0133 30.1626 26.2532 30.1066 30.1693C30.1066 34.1136 27.28 37.3253 23.7259 37.3253ZM47.3178 37.3253C43.8196 37.3253 40.9371 34.1136 40.9371 30.1693C40.9371 26.225 43.7636 23.0133 47.3178 23.0133C50.9 23.0133 53.7545 26.2532 53.6986 30.1693C53.6986 34.1136 50.9 37.3253 47.3178 37.3253Z" fill="black"/>
                  </g>
                  <defs>
                  <clipPath id="clip0">
                  <rect width="71" height="55" fill="white"/>
                  </clipPath>
                  </defs>
                  </svg>
                </a>
              </div>
          </div>
          </header>
          <div className="background-promo" style={{backgroundImage: `url(${skiteampromo})`}} >
           
          </div>
          <Container style={{margin:"25px"}} >
            <p className="original-skiers">
              Original Skiers <br /> on the 
            </p>
            <p className="avax-blockchain-skiers">
              AVAX BLOCKCHAIN
            </p>
            {window.ethereum == null || window.ethereum == undefined?
            <p style={{textAlign:"center"}}>
              <a className="install-metamask" href="https://metamask.io/download" target="_blank">Install Metamask</a>

            </p>
            :
              <>
              {window.user == null ?

                <p style={{textAlign:"center"}}>
                <a onClick={()=>{window.ethereum.enable().then(()=>window.location.reload())}}className="install-metamask" target="_blank">Connect Metamask</a>

                </p> : null
                
              }
              
              </>
            }
            
            <p className="time-till-mint">
              Time till mint - {this.state.timeLeft}
            </p>
          </Container>
          <Container className="meet-skiers-container align-items-center" style={{backgroundImage: `url(${background1})`}}>
            <Grid container spacing={2} >
              <Grid item md={8} xs={12} style={{textAlign:"center"}}>
                <div>
                  <p className="meet-skiers" style={{color:"#435b5b"}}>Meet the skiers</p>
                  <span className="meet-skiers-text">
                    The Avalanche Ski Team are hitting the slopes! <br />
                    They are 10.000 randomly generated Skiers using over 100 unique assets!<br />
                     Each Skier comes with a fresh, unique look, meaning no two Skiers are alike. <br />
                     Let's hit the slopes!
                  </span>
                </div>
              </Grid>
              <Grid item md={4} xs={12}>
                <div style={{textAlign:"center"}}>
                 <img style={{height:"200px"}} src={rotatingArray[this.state.currentImage]} />
                </div>
              </Grid>
            </Grid>
          </Container>

          <Container style={{padding:"35px",paddingTop:"10vh",paddingBottom:"10vh",minHeight:"200px"}}>
            <Grid container spacing={2}>
              <Grid item md={2} xs={2}>
              </Grid>
              <Grid item md={4} xs={12}>
                <div style={{textAlign:"right"}} className="mobile-center mint">
                  <img style={{height:"250px"}} src={skigif} />
                </div>
              </Grid>
              <Grid item md={4} xs={12} className="mobile-center mint">
                <div className="minted-component mobile-center mint" style={{display:"inline-block"}}>
                 {this.state.totalSupply}/{SKI_SUPPLY} <br />
                 skiers minted <br />
                 <div>
                   <span className="skier-select"><a  onClick={()=>{this.decreaseSelect()}}>-</a><div>{this.state.avaxSkiSelect}</div><a onClick={()=>{this.increaseSelect()}}>+</a></span>
                    <a onClick={()=>{this.mint()}}className="skier-jumpin">Jump in</a>
                  </div>
                </div>
                <p className="skier-price">
                  2.5 AVAX / Skier
                </p>
              </Grid>
              <Grid item md={2} xs={2}>
              </Grid>
            </Grid>
          </Container>
          
          <Container  className="tokenomics-container align-items-center" style={{backgroundImage: `url(${background2})`}}>
          <Grid container spacing={2} >
              <Grid item md={8} xs={12} style={{textAlign:"center"}}>
                <div>
                  <p className="meet-skiers" style={{color:"#435b5b"}}>Tokenomics</p>
                  <span className="meet-skiers-text">
                    You can earn 3 different ways with the Avax Ski Team! <br/>
                    10% of every minting fee is given back to existing AvaxSkiTeam holders. <br/>
                    Original minters of a Skier earn 1% royalty each time their Skier is resold on our marketplace.<br />
                    And finally, 3% of EACH transaction on our marketplace is distributed to Skier holders!
                  </span>
                </div>
              </Grid>
              <Grid item md={4} xs={12}>
                <div style={{textAlign:"center"}}>
                 <img style={{height:"200px"}} src={wsb} />
                </div>
              </Grid>
            </Grid>
          </Container>
          <Container className="tokenomics-container">
            <Grid container spacing={2}>
              <Grid item md={4} xs={12} style={{textAlign:"center"}}>
                <div>
                  <p className="meet-skiers" style={{color:"black"}}>Rarity</p>
                  <span className="meet-skiers-text">
                    Skier rarity is determined by how many points the assets add up to. <br/>
                    Your skier can have anywhere from 2 to 14 points. The rares an asset on the Skier is, the more points it gives towards the ranking!
                  </span>
                </div>
              </Grid>
              <Grid item md={8} xs={12}>
                <div style={{textAlign:"center"}}>
                 Rarity chart 
                </div>
              </Grid>
            </Grid>
          </Container>
          <Container  className="roadmap-container align-items-center" style={{backgroundImage: `url(${background3})`}}>
            <Grid container spacing={2}>
              <Grid item md={12} xs={12}>
                <div>
                 <h1 className="roadmap-title">
                   Roadmap
                 </h1>
                 <p className="roadmap-paragraphs">
                   25% Minted: Skier holders will have a chance to receive $AVAX rewarded to them! <br /><br />
                   50% Minted: We will hold a Skier giveaway! 10 Skiers will be given away to Skier holders! <br /><br />
                   75% Minted: More giveaways! 5 more Skiers will be given away to holders! <br /><br />
                   100% Minted: Our exclusive Marketplace goes live! Buy and sell Skiers only on our market and earn rewards doing so!<br />

                 </p>
                </div>
              </Grid>
            </Grid>
          </Container>
          <div style={{textAlign:"center",paddingTop:"100px"}}>
            <img style={{height:"75px"}} src={skiteamlogo} />
          </div>
       </>
    )
  }
}

export default HomePage;
